import React from 'react';
import {Header} from './Header';
import {Footer} from './Footer';
import {Loading} from './Loading';
import {NotificationContainer} from 'react-notifications';
import 'react-notifications/dist/react-notifications.css'
import 'bootstrap/dist/css/bootstrap.css'

export default class Layout extends React.Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        return (
            <>
            {
                this.props.loading ? <Loading/> : 
                    <div className= "d-flex flex-column text-dark" style= {{height: "100vh", overflow: "hidden"}}>
                        <Header collapseToggle= {this.props.collapseToggle} displayUser= {this.props.displayUser}
                             onLogout= {this.props.onLogout} name= {this.props.name} />
                        {this.props.children}
                        <Footer/>
                    </div>
            }
            <NotificationContainer/>
            </>
        );
    }
}