import React from 'react';
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {EditInfo} from './EditInfo';
import {baseUrl, getUserAPI} from '../../APIData';
import 'bootstrap/dist/css/bootstrap.css'
import './PatientInfo.scss';


import girl from '../../assets/images/icons/girl.png';
import boy from '../../assets/images/icons/boy.png';
import calendar from '../../assets/images/icons/calendar.png';
import phone from '../../assets/images/icons/phone.png';
import ID from '../../assets/images/icons/ID.png';
import doctor from '../../assets/images/icons/doctor.png';

import {en2fa} from '../../utils';


export default class PatientInfo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            person: null,
            loading: true,
            editInfoOpen: false,
        }
    }
    
    componentDidMount() {
        this.fetchUserData();
    }
    fetchUserData = () => {
        this.setState({loading: true}, () => {
            axios.get(baseUrl + getUserAPI, {
                headers: {
                    token: localStorage.getItem("token"),
                }
            })
            .then(res => {
                this.setState({person: res.data, loading: false});
                this.props.onNameFetch(`${res.data.first_name} ${res.data.last_name}`);
            })
            .catch(error => {
                if (typeof error.response == "undefined") {
                    ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                }
                else if (String(error.response.status)[0] == 5)
                    ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
                else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            });
        })
    }

    render() {
        return this.state.loading ? null : (
                <div id= "patient-info" className= {`d-felx flex-column justify-content-start align-items-start 
                        ${this.props.showInfoBox ? "" : " hidden"}`}>
                    
                    <div id= "info-body">

                        <div className= "info-category">

                            <div className= "category-header">
                                <div className= "d-flex flex-grow-1 justify-content-start">
                                    <div>
                                        مشخصات کودک
                                    </div>
                                    <button className= "mr-2 no-style" onClick= {() => {this.setState({editInfoOpen: true})}}>
                                        <i class="fas fa-edit" style= {{"font-size": "0.8em"}}></i>
                                    </button>
                                </div>
                            </div>

                            <div className= "category-body"> 
                                <div className= "info-item">
                                    <i className= "info-icon fa fa-user"></i>
                                    <div className= "info-label">
                                        نام
                                    </div>
                                    <div className= "info-value">
                                        {`${this.state.person.first_name} ${this.state.person.last_name}`}
                                    </div>
                                </div>

                                <div className= "info-item">
                                    <i className= "info-icon far fa-calendar-alt"></i>
                                    <div className= "info-label">
                                        بازه سنی
                                    </div>
                                    <div className= "info-value">
                                        {`
                                        ${ this.a = ((new Date()).getFullYear() - (this.state.person.birth_year) - 622)}\
                                        - ${this.a +1}
                                         سال`}
                                    </div>
                                </div>


                                <div className= "info-item">
                                    <i className= "info-icon fa fa-id-card"></i>
                                    <div className= "info-label">
                                        کد ملی
                                    </div>
                                    <div className= "info-value">
                                        {this.state.person.person_id}
                                    </div>
                                </div>

                                <div className= "info-item">
                                    <i className= "info-icon fa fa-venus-mars"></i>
                                    <div className= "info-label">
                                        جنسیت
                                    </div>
                                    <div className= "info-value">
                                        {en2fa[String(this.state.person.gender).toLowerCase()]}
                                    </div>
                                </div>

                            </div>
                        </div>

                        <div className= "info-category">
                            <div className= "category-header">
                                سوابق پزشکی
                            </div>
                            <div className= "category-body">
                                
                            </div>
    

                        </div>

                        <div className= "info-category">
                            <div className= "category-header">
                                اطلاعات تماس
                            </div>
                            <div className= "category-body">
                                <div className= "info-item">
                                    <i className= "info-icon fa fa-phone"></i>
                                    <div className= "info-value">
                                        {this.state.person.phone_number}
                                    </div>
                                </div>
                            </div>
    
                        </div>


                    </div>

                    
                    <EditInfo 
                        isOpen={this.state.editInfoOpen} onClose={() => {this.setState({editInfoOpen: false})}} reloadData={this.fetchUserData}
                        person={this.state.person} key={this.state.person.person_id} 
                    />
                </div>
   
        );
    }
}