import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './CountDown.scss';

import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';

const progressBarInterval = 0.1;
export default class CountDown extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            seconds: this.props.duration,
        }
        this.timer = null;
    }

    startTimer = () => {
        if (this.props.halt) {
            this.disableTimer();
            return;
        }

        clearInterval(this.timer);
        this.setState({seconds: this.props.duration}, () => {
            this.timer = setInterval(this.countDown, progressBarInterval*1000);
        })
    }



    disableTimer = () => {
        clearInterval(this.timer);
        this.setState({seconds: 0});
    }

    countDown = () => {
        if (this.props.halt) {
            this.disableTimer();
            return;
        }
        if (this.state.seconds > 0) {
            this.setState({seconds: this.state.seconds-progressBarInterval <= 0 ? 0 : this.state.seconds-progressBarInterval}, () => {
                if (this.state.seconds == 0 && typeof this.props.notifyCountEnd === 'function') {
                    clearInterval(this.timer);
                    this.props.notifyCountEnd();
                }
            });
        }
    }

    getTime = () => {
        return `${Math.floor((this.state.seconds + 1 - progressBarInterval)/60)} :
            ${String("0" + Math.floor((this.state.seconds + 1 - progressBarInterval)%60)).slice(-2)}`;
    }

    componentDidMount() {
        this.startTimer();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.halt && this.props.halt != true)
            this.disableTimer();
    }
    componentWillUnmount() {
        clearInterval(this.timer);
    }

    render() {
        return (
            <div id= "count-down" className= {`${this.props.halt ? "disabled" : ""}`}>
                <CircularProgressbar 
                    minValue={0} maxValue= {this.props.duration} value={this.state.seconds} text={this.getTime()}
                    strokeWidth={3}
                />
            </div>
        );
    }
}