import React from 'react';
import {NotificationManager} from 'react-notifications';
import axios from 'axios';
import { withRouter } from "react-router-dom";
import {StageLayout} from './Stages';
import {PreStart} from './Stages/PreStart';
import {GameStage} from './Stages/GameStage';
import {WheelStage} from './Stages/WheelStage';
import {ParrotStage} from './Stages/ParrotStage';
import DoubleCheckModal from '../../DoubleCheckModal/DoubleCheckModal';
import {baseUrl, endSessionAPI, newSessionAPI, sessionInfoAPI, startWheelAPI, startParrotAPI, stopParrotAPI} from '../../../APIData';
import { persianTime } from '../../../utils';

import './SessionControl.scss';

class SessionControl extends React.Component {
    constructor(props) {
        super(props);
        this.stages = {
            PRE_START: {
                name: 'PRE_START',
                order: 0,
                api: newSessionAPI,
                nextStage: 'GAME_STAGE',
            },
            GAME_STAGE: {
                name: 'GAME_STAGE',
                faName: 'استیج بازی',
                order: 1,
                api: startWheelAPI,
                nextStage: 'WHEEL_STAGE',
                duration: 120,
                iconClass: "fas fa-gamepad",
            },
            WHEEL_STAGE: {
                name: 'WHEEL_STAGE',
                faName: 'استیج چرخ نوری',
                order: 2,
                api: startParrotAPI,
                nextStage: 'PARROT_STAGE',
                duration: 120,
                iconClass: "fas fa-cog",
            },
            PARROT_STAGE: {
                name: 'PARROT_STAGE',
                faName: 'استیج طوطی',
                order: 3,
                api: stopParrotAPI,
                nextStage: 'PRE_START',
                duration: 120,
                iconClass: "fas fa-dove",
            },
        }
        this.state = {
            stageInfoLoading: true,
            stage: this.stages.PRE_START,
            isSessionCloseDoubleCheckOpen: false,
        }
    }

    componentDidMount() {
        this.fetchStageData();
    }

    fetchStageData = () => {
        this.setState({stageInfoLoading: true}, () => {
            axios.get(baseUrl + sessionInfoAPI, {
                headers: {
                    token: localStorage.getItem("token"),
                }
            })
            .then(res => {
                this.stages[res.data.stage.name].duration = res.data.stage.duration;
                this.setState({stage: this.stages[res.data.stage.name], stageInfoLoading: false}, () => {
                    this.props.onStageChange(this.state.stage.name);
                });
            })
            .catch(error => {
                if (typeof error.response == "undefined") {
                    ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                }
                else if (String(error.response.status)[0] == 4) {
                    this.setState({stage: this.stages.PRE_START, stageInfoLoading: false}, () => {
                        this.props.onStageChange(this.state.stage.name);
                    });
                }
                else if (String(error.response.status)[0] == 5)
                    ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
                else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            });
        });
    }


    nextStage = () => {
        axios.get(baseUrl + this.state.stage.api, {
            headers: {
                token: localStorage.getItem("token"),
            }
        })
        .then(res => {
            if (this.state.stage.name == this.stages.PARROT_STAGE.name) {
                ((message) => {NotificationManager.success(message);})('تست با موفقیت به اتمام رسید');
                this.fetchStageData();
            }
            else {
                this.stages[this.state.stage.nextStage].duration = res.data.duration;
                this.setState({stage: this.stages[this.state.stage.nextStage]}, () => {
                    this.props.onStageChange(this.state.stage.name);
                });
            }
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }

    endSession = () => {
        axios.get(baseUrl + endSessionAPI, {
            headers: {token: localStorage.getItem("token")},
        })
        .then(res => {
            this.fetchStageData();
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }

    endSessionRequest = () => {
        this.setState({isSessionCloseDoubleCheckOpen: true});
    }
    
    
    render() {
        const stage = this.state.stage.name == this.stages.PRE_START.name ?
                            <PreStart 
                                key= {this.state.stage == this.stages.PRE_START}
                                notifyEnd= {this.nextStage} 
                                isCurrentStage= {this.state.stage == this.stages.PRE_START}
                                siteProperties= {this.props.siteProperties}
                            />
                        : this.state.stage.name == this.stages.GAME_STAGE.name ?
                            <GameStage 
                                key= {this.state.stage == this.stages.GAME_STAGE}
                                notifyEnd= {this.nextStage} 
                                duration= {this.stages.GAME_STAGE.duration} 
                                isCurrentStage= {this.state.stage == this.stages.GAME_STAGE}
                            />

                        : this.state.stage.name == this.stages.WHEEL_STAGE.name ?

                            <WheelStage 
                                key= {this.state.stage == this.stages.WHEEL_STAGE}
                                notifyEnd= {this.nextStage} 
                                duration= {this.stages.WHEEL_STAGE.duration} 
                                isCurrentStage= {this.state.stage == this.stages.WHEEL_STAGE}
                            />

                        : this.state.stage.name == this.stages.PARROT_STAGE.name ?

                            <ParrotStage
                                key= {this.state.stage == this.stages.PARROT_STAGE}
                                notifyEnd= {this.nextStage} 
                                duration= {this.stages.PARROT_STAGE.duration} 
                                isCurrentStage= {this.state.stage == this.stages.PARROT_STAGE}
                            />
                        : null;

        return this.state.stageInfoLoading ? null : (
            <div id= "session-control">
                <StageLayout
                    stage= {this.state.stage}
                    onEndSession= {this.endSessionRequest}
                    onStageChange= {this.nextStage}
                >
                        <PreStart 
                            notifyEnd= {this.nextStage} 
                            isCurrentStage= {this.state.stage == this.stages.PRE_START}
                            isDead= {this.state.stage.order > this.stages.PRE_START.order}
                            siteProperties= {this.props.siteProperties}
                        />
                        <GameStage 
                            notifyEnd= {this.nextStage} 
                            duration= {this.stages.GAME_STAGE.duration} 
                            isCurrentStage= {this.state.stage == this.stages.GAME_STAGE}
                            isDead= {this.state.stage.order > this.stages.GAME_STAGE.order}
                        />
                        <WheelStage 
                            notifyEnd= {this.nextStage} 
                            duration= {this.stages.WHEEL_STAGE.duration} 
                            isCurrentStage= {this.state.stage == this.stages.WHEEL_STAGE}
                            isDead= {this.state.stage.order > this.stages.WHEEL_STAGE.order}
                        />
                        <ParrotStage
                            notifyEnd= {this.nextStage} 
                            duration= {this.stages.PARROT_STAGE.duration} 
                            isCurrentStage= {this.state.stage == this.stages.PARROT_STAGE}
                            isDead= {this.state.stage.order > this.stages.PARROT_STAGE.order}
                        />

                </StageLayout>
                <DoubleCheckModal 
                    isOpen= {this.state.isSessionCloseDoubleCheckOpen}
                    onClose= {() => {this.setState({isSessionCloseDoubleCheckOpen: false})}}
                    onYes= {this.endSession}
                    title= "لغو تست؟"
                    body= "با اتمام تست در این زمان دیگر امکان ادامه آن را نخواهید داشت. آیا مطمئن هستید؟"
                />
            </div>
        );
    }
}

export default withRouter(SessionControl);