import React from 'react';
import {isValidIranianNationalCode, nameRegex} from '../../utils';
import persianDate from 'persian-date';

String.prototype.isEmpty = function() {
    return (this.length === 0 || !this.trim());
};

const minAge = 2;
const minBirthYear = 1380;

export default class PersonInfoValidator extends React.Component {
    constructor(props) {
        super(props);
        this.formData = {
            firstName: {
                apiField: 'first_name',
                data: '',
                isValid: null,
                required: true,
                validationMessage: '',
                validationFunction: this.isNameValid,
                invalidFeedback: '',
                defaultInvalidFeedback: 'مثلا:‌ محمد',
            },
            lastName: {
                apiField: 'last_name',
                data: '',
                isValid: null,
                required: true,
                validationFunction: this.isNameValid,
                invalidFeedback: '',
                defaultInvalidFeedback: 'مثلا: محمدی',
            },
            birthYear: {
                apiField: 'birth_year',
                data: '',
                isValid: null,
                required: true,
                validationFunction: this.isBirthYearValid,
                invalidFeedback: '',
                defaultInvalidFeedback: `سال تولد باید عددی بین ${minBirthYear} تا ${new persianDate().State.persianAstro.year - minAge} باشد`,
            },
            phoneNumber: {
                apiField: 'phone_number',
                data: '',
                isValid: null,
                required: true,
                validationFunction: this.isPhoneNumberValid,
                invalidFeedback: '',
                defaultInvalidFeedback: 'شماره موبایل وارد شده صحیح نیست',
            },
            personID: {
                apiField: 'person_id',
                data: localStorage.getItem("personID") || '',
                isValid: null,
                required: true,
                validationFunction: this.isPersonIDValid,
                invalidFeedback: '',
                defaultInvalidFeedback: 'کد ملی وارد شده صحیح نیست',
            },
            gender: {
                apiField: 'gender',
                data: '',
                isValid: null,
                required: true,
                validationFunction: this.isGenderValid,
                invalidFeedback: '',
                defaultInvalidFeedback: '',
            },
            medicalRecord: {
                apiField: 'medical_info',
                data: '',
                isValid: null,
                required: true,
                validationFunction: this.isMedicalRecordValid,
                invalidFeedback: '',
                defaultInvalidFeedback: '',
            },
        }
    }
    
    inputChange = (fieldName, event, errorEmptyField= false) => {
        this.formData[fieldName].data = event.target.value;
        if (this.formData[fieldName].isValid != null)
            this.validateField(fieldName, errorEmptyField);
    }

    validateField = (fieldName, errorEmptyField= false) => {
        let field = this.formData[fieldName];
        if (!errorEmptyField && field.data.isEmpty()) {
            field.isValid = true;
        }
        else if (field.required && field.data.isEmpty()) {
            field.isValid = false;
            field.invalidFeedback = 'این فیلد نمی‌تواند خالی باشد';
        }
        else if (!field.validationFunction(field.data)) {
            field.isValid = false;
            field.invalidFeedback = field.defaultInvalidFeedback;
        }

        else field.isValid = true;
        this.setState(this.state); 
        return field.isValid;
    }

    isNameValid(name) {
        return nameRegex.test(name);
    }

    isPhoneNumberValid = (phone) => {
        return (/^\d*$/.test(phone)) && phone.length >= 10 && phone.length <= 12;
    }

    isPersonIDValid = (id) => {
        return isValidIranianNationalCode(id);
    }

    isBirthYearValid = (year) => {
        return (year >= minBirthYear && year <= (new persianDate().State.persianAstro.year - minAge));
    }

    isGenderValid = (gender) => {
        return gender == "male" || gender == "female";
    }

    isMedicalRecordValid = (medicalRecord) => {
        return medicalRecord == "ADHD" || medicalRecord == "autism" || medicalRecord == "normal";
    }

    render() {
        return false;
    }
}