import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './SwitchOptionWrapper.scss';

export default class SwitchOption extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className= {`option d-flex flex-row justify-content-between align-items-center my-3 ${typeof this.props.className == "undefined" ? "" : this.props.className}`}>
                <div className= "title">{this.props.title}</div>
                <div className= "switch-button mr-4">
                    {this.props.children}
                </div>
            </div>
        );
    }
}