import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './PreStart.scss';

export default class PreStart extends React.Component {
    constructor(props) {
        super(props);
        this.showInfo = true;
    }
    
    render() {
        return (
            <div id= "pre-start" className= {`d-flex flex-column align-items-center h-100 mt-4 stage-as-slide 
                ${this.props.isCurrentStage ? " current" : this.props.isDead ? " dead" : ""}`}>
                <div className= "d-flex flex-column align-items-center w-100">
                    <div className= "d-flex flex-column align-items-center w-100">
                        <div id= "help">
                            <div className="card">
                                <div className="card-header text-white bold">تست غربالگری اتیسم</div>
                                <div className="card-body bg-light text-secondary">
                                    <small dangerouslySetInnerHTML={{ __html: this.props.siteProperties.prestart_help }}></small>
                                </div>
                            </div>
                        </div>
                        <div className= "my-5">
                            <button className= "button btn btn-primary primary-button bold" onClick= {this.props.notifyEnd}>شروع تست</button>
                        </div>
                    </div>
                </div>

            </div>


        );
    }
}