import React from 'react';
import './Header.scss';
import 'bootstrap/dist/css/bootstrap.css'
import logo from '../../../assets/images/logo.png';
import bars from '../../../assets/images/bars.png';

class NameBox extends React.Component{

    render() {
        return (
            <div className= "namebox-wrapper d-flex align-items-center">
                <div class= "namebox btn btn-outline-dark d-flex align-items-center">
                    <i class="fa fa-user mr-1 ml-2" aria-hidden="true"></i>
                    <span>{this.props.name}</span>
                </div>
                <button title= "خروج" onClick= {this.props.onLogout}
                    class= "btn btn-outline-dark d-flex align-items-center justify-content-center logout-button">
                    <i class="fas fa-sign-out-alt" ></i>
                </button>

            </div>
        );
    }
}

export default class Header extends React.Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        return (

            <nav id= "header" className="navbar justify-content-between">
                <div>
                    <a href="/"> <img id="logo" className="navbar-brand" src={logo}/> </a>
                </div>
                
                <div className= "right-side d-flex flex-row-reverse justify-content-between align-items-center">
                    <div>
                        <i class="bar text-dark fa fa-bars" onClick= {this.props.collapseToggle}></i>
                    </div>
                    {this.props.displayUser ? <NameBox name= {this.props.name} onLogout= {this.props.onLogout} /> : null}
                </div>


            </nav>
        );
    }
}