import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './TimeProgressBar.scss';
import LinearProgress from '@material-ui/core/LinearProgress';

const progressBarInterval = 0.1;
export default class TimeProgressBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            seconds: 0,
            active: false,
        }
    }


    startTimer = () => {
        clearInterval(this.timer);
        this.setState({seconds: 0, active: true}, () => {
            this.timer = setInterval(this.countUp, progressBarInterval*1000);
        })
    }



    disableTimer = () => {
        clearInterval(this.timer);
        this.setState({seconds: 0, active: false});
    }

    countUp = () => {
        if (!this.state.active) {
            this.disableTimer();
            return;
        }
        if (this.state.seconds < this.props.duration) {
            this.setState({seconds: this.state.seconds+progressBarInterval >= this.props.duration ? this.props.duration
                        : this.state.seconds+progressBarInterval}, () => {
                if (this.state.seconds >= this.props.duration) {
                    this.disableTimer();
                    if (typeof this.props.notifyTimerEnd === 'function') 
                        this.props.notifyTimerEnd();
                }
            });
        }
    }

    componentDidMount() {
        this.startTimer();
    }

    componentWillReceiveProps(nextProps) {
        if (!nextProps.active && this.state.active)
            this.disableTimer();
        else if (nextProps.active && !this.state.active)
            this.startTimer();
    }
    componentWillUnmount() {
        clearInterval(this.timer);
    }

    
    componentDidMount() {

    }
    render() {
        if (typeof this.props.duration == "undefined" || this.props.duration == 0 || !this.props.active)
            return <div className= ""></div>;
        return (
            <div className= "progress-bar" style= {{"width": "100%"}}>
                <LinearProgress value= {(this.state.seconds*100)/this.props.duration} valueBuffer= {100} variant= "determinate"/>
            </div>
        );
    }
}