import React from 'react';
import persianDate from 'persian-date';
import axios from 'axios';
import {PersonInfoValidator} from '../FormValidator';
import { withRouter } from "react-router-dom";
import {NotificationManager} from 'react-notifications';
import {Layout} from '../Layout';
import {isValidIranianNationalCode, nameRegex, mapObject, changeKeys} from '../../utils';
import { baseUrl, signupAPI, loginAPI } from '../../APIData';

import './SignUp.scss';
import 'bootstrap/dist/css/bootstrap.css'

import male from '../../assets/images/icons/boy.png';
import calendar from '../../assets/images/icons/calendar.png';
import phone from '../../assets/images/icons/phone.png';
import gender from '../../assets/images/icons/gender.png';
import ID from '../../assets/images/icons/ID.png';
import doctor from '../../assets/images/icons/doctor.png';

const minAge = 2;
const minBirthYear = 1380;

class SignUp extends PersonInfoValidator {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
        }

    }

    componentDidMount() {
        if (localStorage.getItem("token") != null) {
            this.props.history.push('/');
            return;
        }
        this.setState({loading: false})
    }


    submitForm = () => {
        let valid = true;
        for (const fieldName in this.formData) {
            valid &= this.validateField(fieldName, true);
        }
        if (!valid) return;

        localStorage.setItem("personID", this.formData.personID.data);
        
        let payload = mapObject(this.formData, function(field) {
            return field.data;
        });

        const keyMap = mapObject(this.formData, function(field) {
            return field.apiField;
        });

        payload = changeKeys(payload, keyMap);

        axios.post(baseUrl + signupAPI, payload)
        .then(signupRes => {
            axios.post(baseUrl + loginAPI, {person_id: localStorage.getItem("personID")})
            .then(loginRes => {
                localStorage.removeItem("personID");
                localStorage.setItem("token", loginRes.data.token)
                this.props.history.push('/control');
            })
            .catch(loginError => {
                if (loginError.response.status == 401) {
                    ((message) => {NotificationManager.error(message);})('کاربری با کد ملی واردشده قبلا ثبت نام کرده');
                }
                else if (String(loginError.response.status)[0] == 5)
                    ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
                else ((message) => {NotificationManager.error(message);})(loginError.response.data.detail);
            });
        })
        .catch(signupError => {
            if (String(signupError.response.status)[0] == 5)
                ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
            else ((message) => {NotificationManager.error(message);})(signupError.response.data.detail);
        });
    }

    render() {
        return (
            <Layout loading= {this.props.loading}>
                <div id= "Root" className= "container">
                    <form>
                        <div className= "d-flex justify-content-between form-entry">
                            <div className= "d-sm-flex flex-row-reverse">
                                <div className= "form-group">
                                    <input className={`form-control ${this.formData.firstName.isValid == false ? "is-invalid" : ""}`}
                                        v-model="personData.first_name" id="first_name" placeholder="نام"
                                        onChange= {(event) => {this.inputChange('firstName', event)} }
                                        onBlur= {() => {this.validateField('firstName')}}/>
                                    <div className="invalid-feedback">{this.formData.firstName.invalidFeedback} </div>
                                </div>
                                <div className= "form-group">
                                    <input className={`form-control ${this.formData.lastName.isValid == false ? "is-invalid" : ""}`} 
                                        v-model="personData.last_name" id="last_name" placeholder="نام خانوادگی"
                                        onChange= {(event) => {this.inputChange('lastName', event)} }
                                        onBlur= {() => {this.validateField('lastName')}}/>
                                    <div className="invalid-feedback">{this.formData.lastName.invalidFeedback} </div>
                                </div>
                            </div>
                            <div>
                                <label for="names">
                                    <img className= "icon" src={male}/>
                                </label>
                            </div>
                        </div>

                        <div className= "d-flex justify-content-between form-entry">
                            <div className= "form-group">
                                <input className={`form-control ${this.formData.birthYear.isValid == false ? "is-invalid" : ""}`}
                                    id="birthYear" placeholder="سال تولد به هجری شمسی"
                                    onChange= {(event) => {this.inputChange('birthYear', event)} }
                                    onBlur= {() => {this.validateField('birthYear')}}/>
                                
                                <div className="invalid-feedback">{this.formData.birthYear.invalidFeedback} </div>
                            </div>
                            <div>
                                <label for="birthYear">
                                    <img className= "icon" src={calendar}/>
                                </label>
                            </div>
                        </div>

                        <div className= "d-flex justify-content-between form-entry">
                            <div className= "form-group">
                                <input className={`form-control ${this.formData.phoneNumber.isValid == false ? "is-invalid" : ""}`}
                                    v-model="personData.phone_number" id="phone_number" placeholder="شماره تلفن"
                                    onChange= {(event) => {this.inputChange('phoneNumber', event)} }
                                    onBlur= {() => {this.validateField('phoneNumber')}}/>  
                                <div className="invalid-feedback">{this.formData.phoneNumber.invalidFeedback} </div>      
                            </div>

                            <div>
                                <label for="phone_number">
                                    <img className= "icon" src={phone}/>
                                </label>
                            </div>
                        </div>

                        <div className= "d-flex justify-content-between form-entry">
                            <div className= "form-group">
                                <input className={`form-control ${this.formData.personID.isValid == false ? "is-invalid" : ""}`}
                                    v-model="personData.personID" id="personID" 
                                    placeholder= {localStorage.getItem("personID") == null ? "شماره ملی" : localStorage.getItem("personID")}
                                    onChange= {(event) => {this.inputChange('personID', event)} }
                                    onBlur= {() => {this.validateField('personID')}}/>            
                                <div className="invalid-feedback">{this.formData.personID.invalidFeedback} </div> 
                            </div>
                            <div>
                                <label for="personID">
                                    <img className= "icon" src={ID}/>
                                </label>
                            </div>
                        </div>

                        <div className= "d-flex justify-content-between form-entry">
                            <div className= "form-group">
                                <select className={`form-control ${this.formData.gender.isValid == false ? "is-invalid" : ""}`}
                                    v-model="personData.gender" aria-placeholder="جنسیت" 
                                    onChange= {(event) => {this.inputChange('gender', event)} }>
                                    <option disabled selected value=""> جنسیت </option>
                                    <option value="male"> پسر </option>
                                    <option value="female"> دختر </option>
                                </select>               
                                <div className="invalid-feedback">لطفا جنیست کودک را انتخاب کنید</div>                      
                            </div>
                            <div>
                                <label for="gender">
                                    <img className= "icon" src={gender}/>
                                </label>
                            </div>
                        </div>
                        <div className= "d-flex justify-content-between form-entry">
                            <div className= "form-group">
                                <select className={`form-control ${this.formData.medicalRecord.isValid == false ? "is-invalid" : ""}`}
                                    onChange= {(event) => {this.inputChange('medicalRecord', event)} }>
                                    <option disabled selected value=""> سابقه پزشکی  </option>
                                    <option value="ADHD"> ADHD </option>
                                    <option value="autism"> اتیسم </option>
                                    <option value="normal"> بدون سابقه پزشکی  </option>
                                </select>               
                                <div className="invalid-feedback">لطفا یک گزینه را انتخاب کنید</div>                      
                            </div>
                            <div>
                                <label for="medicalRecord">
                                    <img className= "icon" src={doctor}/>
                                </label>
                            </div>
                        </div>
                        
                        <div className= "d-flex justify-content-center form-entry">
                            <button id= "submit-btn" type= "submit" className= "btn btn-primary primary-button"
                                onClick= {(event) => {
                                    event.preventDefault(); 
                                    this.submitForm();
                                }}
                            >
                                ثبت نام
                            </button>
                        </div>
                    </form>
                </div>
            </Layout>
        );
    }
}

export default withRouter(SignUp);