import React from 'react';
import {TimeProgressBar} from '../../../../TimeProgressBar';
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {baseUrl, parrotCommandsAPI, performParrotCommandAPI} from '../../../../../APIData';

import 'bootstrap/dist/css/bootstrap.css'
import './CommandBox.scss';

export default class CommandBox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isPerformingCommand: false,
            currentCommandDuration: 0,
        }
        this.movementCommands = [];
        this.voiceCommands = [];
    }

        
    componentDidMount() {
        axios.get(baseUrl + parrotCommandsAPI, {
            headers: {
                token: localStorage.getItem("token"),
            }
        })
        .then(res => {
            this.movementCommands = res.data.movement;
            this.voiceCommands = res.data.voice;
            this.setState(this.state);
        })
        .catch(error => {
            if (typeof error.response == "undefined"){
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                return;
            }
            ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });

    }

    sendParrotCommand = (command) => {
        if (this.props.parrotAutoMode)
            return;
        
        this.setState({isPerformingCommand: true, currentCommandDuration: command.perform_time}, () => {
            axios.post(baseUrl + performParrotCommandAPI, 
                {commandID: command.id}, 
                {headers: {token: localStorage.getItem("token")}}
            )
            .then(res => {
            //    ((message) => {NotificationManager.success(message);})('دستور ارسال شد');
            })
            .catch(error => {
                if (typeof error.response == "undefined"){
                    ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                    return;
                }
                ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            });
        })

    }

    onCommandDurationReach = () => {
    //  NotificationManager.info('اجرای دستور به اتمام رسید')
        this.setState({isPerformingCommand: false});
    }

    renderCommands(list) {
        let commands = []
        for (const item of list) {
            commands.push(
                <div className= "button-surrounding">
                    <button className= "command-btn" onClick= {() => {this.sendParrotCommand(item)}}>{item.title}</button>
                </div>
            );
        }
        return commands;
    }


    render() {
        return (
                <div id= "control-box" className= {`row flex-row justify-content-center align-items-stretch mt-4
                                    ${this.props.parrotAutoMode == null ? " invisible" : this.props.parrotAutoMode ? " hidden" : " visible"}
                                    ${this.state.isPerformingCommand ? " disabled" : ""}`}>
                    <TimeProgressBar active= {this.state.isPerformingCommand} duration= {this.state.currentCommandDuration}
                        notifyTimerEnd= {this.onCommandDurationReach}/>
                    <div className= "col-md-6 col-10 align-items-center temp has-border rounded pt-2">
                        <div>دستورات حرکتی</div>
                        <div className= "command-box mt-3 d-flex flex-row justify-content-center flex-wrap ">
                            {this.renderCommands(this.movementCommands)}
                        </div>
                    </div>
                    <div className= "col-md-6 col-10 flex-column align-items-center temp rounded pt-2">
                        <div>دستورات صوتی</div>
                        <div className= "command-box mt-3 d-flex flex-row justify-content-center flex-wrap">
                            {this.renderCommands(this.voiceCommands)}
                        </div>
                    </div>
                </div>
            );
    }
}