import React from 'react';
import {Layout} from '../Layout';
import axios from 'axios';
import { PersonInfoValidator } from '../FormValidator';
import { withRouter } from "react-router-dom";
import {NotificationManager} from 'react-notifications';
import {isValidIranianNationalCode} from '../../utils';
import './Login.scss';
import 'bootstrap/dist/css/bootstrap.css'

import {baseUrl, loginAPI} from '../../APIData';

String.prototype.isEmpty = function() {
    return (this.length === 0 || !this.trim());
};


class Login extends PersonInfoValidator {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
        }
        this.formData = {
            personID: {
                apiField: 'person_id',
                data: '',
                isValid: null,
                required: true,
                validationMessage: '',
                validationFunction: isValidIranianNationalCode,
                defaultInvalidFeedback: 'کد ملی وارد شده صحیح نیست',
                invalidFeedback: '',
            }
        }
    }
    
    
    componentDidMount() {
        if (localStorage.getItem("token") != null) {
            this.props.history.push('/');
            return;
        }
        this.setState({loading: false});
    }

    submitPersonID = () => {
        if (!this.validateField('personID', true))
            return;
        axios.post(baseUrl + loginAPI, {person_id: this.formData.personID.data})
        .then(res => {
            localStorage.setItem("token", res.data.token)
            this.props.history.push('/control');
        })
        .catch(error => {
            if (error.response.status == 401) {
                localStorage.setItem("personID", this.formData.personID.data);
                this.props.history.push('/signup');
            }
            else if (String(error.response.status)[0] == 5)
                ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }

    render() {
        return (
            <Layout loading= {this.state.loading}>
                <div id="Root" className= "container"> 
                <form>
                    <div className= "d-flex flex-column align-items-center main-form">
                        <div className= "form-group ">
                            <input v-model="personID" placeholder="شماره ملی"
                                onChange= {(event) => {
                                    this.inputChange('personID', event, true)
                                }}
                                onBlur= {() => {
                                    this.validateField('personID', true)
                                }}
                                className= {"form-control " + (this.formData.personID.isValid == null ? '' 
                                    : this.formData.personID.isValid ? 'is-valid' : 'is-invalid')} />
                                <div className="invalid-feedback">{this.formData.personID.invalidFeedback}</div>
                        </div>
                        <button id= "button" className= "btn btn-primary primary-button" 
                            onClick= {(event) => {
                                event.preventDefault();
                                this.submitPersonID();
                            }}
                        >
                            شروع
                        </button>
                    </div>
                </form>
                </div>
            </Layout>
        );
    }
}
export default withRouter(Login);