import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './ParrotStage.scss';

import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import SwitchOptionWrapper from '../SwitchOptionWrapper';
import CommandBox from './CommandBox';
import axios from 'axios';
import {baseUrl, setParrotModeAPI} from '../../../../../APIData';
import {NotificationManager} from 'react-notifications';

export default class ParrotStage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            parrotAutoMode: false,
        }
    }

    alertModeToggle = (mode) => {
        axios.post(baseUrl + setParrotModeAPI, 
            {mode: mode}, 
            {headers: {token: localStorage.getItem("token")}}
        )
        .then(res => {
        })
        .catch(error => {
            if (typeof error.response == "undefined"){
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                return;
            }
            ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }
    render() {
        
        return (
            <div id= "parrot-stage" className= {`d-flex flex-column align-items-center w-100 stage-as-slide parrot 
                ${this.props.isCurrentStage ? " current" : this.props.isDead ? " dead" : ""}`}>
                <div className= "stage-control d-flex flex-column align-items-center">
                    <div class= "d-flex flex-column align-items-stretch">
                        <SwitchOptionWrapper title= "دستورات طوطی" className= "onoff-switch">
                                <BootstrapSwitchButton
                                    checked={this.state.parrotAutoMode}
                                    onlabel='خودکار'
                                    onstyle='warning'
                                    offlabel='دستی'
                                    offstyle='info'
                                    style='w-100 mx-3'
                                    onChange={(checked) => {
                                        this.setState({parrotAutoMode: checked});
                                        this.alertModeToggle(checked ? "auto" : "manual");
                                    }}
                                />
                        </SwitchOptionWrapper>
                    </div>

                    <CommandBox parrotAutoMode= {this.state.parrotAutoMode}/>
                </div>
            </div>
        );
    }
}