import React from 'react';
import {NotificationManager} from 'react-notifications';
import axios from 'axios';
import {PersonInfoValidator} from '../../FormValidator';

import 'bootstrap/dist/css/bootstrap.css'
import './EditInfo.scss';
import {en2fa} from '../../../utils'
import { baseUrl, updateUserInfoAPI } from '../../../APIData';
import {mapObject, changeKeys} from '../../../utils';

import { Button, Modal, ModalBody, ModalFooter } from 'reactstrap';

String.prototype.toPersian = function() {
    return en2fa[this.toLowerCase()];
};

export default class EditInfo extends PersonInfoValidator {
    constructor(props) {
        super(props);
        this.state = {

        }

        for (let field of Object.values(this.formData)) {
            field.data = String(this.props.person[field.apiField]);
        }
    }
    
    _inputChange = (fieldName, event) => {
        this.inputChange(fieldName, event);
        if (this.formData[fieldName].data.length === 0 || !this.formData[fieldName].data.trim()) {
            this.formData[fieldName].data = String(this.props.person[this.formData[fieldName].apiField]);
            this.formData[fieldName].isValid = true;
        }
    }

    sendData = () => {
        let valid = true;
        for (const fieldName in this.formData) {
            valid &= this.validateField(fieldName, true);
        }
        if (!valid) return;
        
        let payload = mapObject(this.formData, function(field) {
            return field.data;
        });

        const keyMap = mapObject(this.formData, function(field) {
            return field.apiField;
        });

        payload = changeKeys(payload, keyMap);

        axios.put(baseUrl + updateUserInfoAPI, payload, {
            headers: {
                token: localStorage.getItem("token"),
            }
        })
        .then(res => {
            this.props.reloadData();
            ((message) => {NotificationManager.success(message);})('مشخصات با موفقیت ویرایش شد');
            this.props.onClose();
        })
        .catch(error => {
            if (String(error.response.status)[0] == 5)
                ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });

    }
    render() {
        return (
                <Modal isOpen={this.props.isOpen} className={this.props.className}>  
                    <ModalBody>
                    <div id= "edit-info">
                        <form>
                            <div className= "d-flex justify-content-between form-entry">
                                <div className= "d-sm-flex flex-row-reverse align-items-start justify-content-between">
                                    <div className= "input-group ml-3">
                                        <input className={`form-control ${this.formData.firstName.isValid == false ? " is-invalid" : " "}`}
                                            placeholder={this.props.person.first_name}
                                            onChange= {(event) => {this._inputChange('firstName', event)} }
                                            onBlur= {() => {this.validateField('firstName')}}/>
                                        <div class="input-group-append">
                                            <span class="input-group-text"><i class="fa fa-user"></i></span>
                                        </div>
                                        <div className="invalid-feedback">{this.formData.firstName.invalidFeedback} </div>
                                    </div>
                                    <div className= "input-group has-feedback">
                                        <input className={`form-control ${this.formData.lastName.isValid == false ? "is-invalid" : ""}`} 
                                            placeholder={this.props.person.last_name}
                                            onChange= {(event) => {this._inputChange('lastName', event)} }
                                            onBlur= {() => {this.validateField('lastName')}}/>
                                        <div class="input-group-append">
                                            <span class="input-group-text"><i class="fa fa-user"></i></span>
                                        </div>
                                        <div className="invalid-feedback">{this.formData.lastName.invalidFeedback} </div>
                                    </div>
                                </div>
                            </div>

                            <div className= "d-flex justify-content-between form-entry">
                                <div className= "input-group has-feedback">
                                    <input className={`form-control`} readOnly
                                        placeholder={this.props.person.person_id}/>
                                    <div class="input-group-append">
                                        <span class="input-group-text"><i class="fa fa-id-card"></i></span>
                                    </div>
                                    <div className="invalid-feedback">{this.formData.personID.invalidFeedback} </div>
                                </div>
                            </div>

                            <div className= "d-flex justify-content-between form-entry">
                                <div className= "input-group has-feedback">
                                    <input className={`form-control ${this.formData.phoneNumber.isValid == false ? "is-invalid" : ""}`}
                                        placeholder={this.props.person.phone_number}
                                        onChange= {(event) => {this._inputChange('phoneNumber', event)} }
                                        onBlur= {() => {this.validateField('phoneNumber')}}/>
                                    <div class="input-group-append">
                                        <span class="input-group-text"><i class="fa fa-phone"></i></span>
                                    </div>
                                    <div className="invalid-feedback">{this.formData.phoneNumber.invalidFeedback} </div>
                                </div>
                            </div>

                            <div className= "d-flex justify-content-between form-entry">
                                <div className= "input-group has-feedback">
                                    <input className={`form-control ${this.formData.birthYear.isValid == false ? "is-invalid" : ""}`}
                                        placeholder={this.props.person.birth_year}
                                        onChange= {(event) => {this._inputChange('birthYear', event)} }
                                        onBlur= {() => {this.validateField('birthYear')}}/>
                                    <div class="input-group-append">
                                        <span class="input-group-text"><i class="far fa-calendar-alt"></i></span>
                                    </div>
                                    <div className="invalid-feedback">{this.formData.birthYear.invalidFeedback} </div>
                                </div>
                            </div>

                            <div className= "d-flex justify-content-between form-entry">
                                <div className= "input-group has-feedback">
                                    <select className={`form-control`}
                                        onChange= {(event) => {this._inputChange('gender', event)} }>
                                        <option disabled selected value=""> {String(this.props.person.gender).toPersian()} </option>
                                        <option value="male"> پسر </option>
                                        <option value="female"> دختر </option>
                                    </select>               
                                    <div class="input-group-append">
                                        <span class="input-group-text"><i class="fa fa-venus-mars"></i></span>
                                    </div>
                                </div>
                            </div>


                            <div className= "d-flex justify-content-between form-entry">
                                <div className= "input-group has-feedback">
                                    <select className={`form-control`}
                                        onChange= {(event) => {this._inputChange('medicalRecord', event)} }>
                                        <option disabled selected value=""> {String(this.props.person.medical_info).toPersian()}  </option>
                                        <option value="ADHD"> ADHD </option>
                                        <option value="autism"> اتیسم </option>
                                        <option value="normal"> بدون سابقه پزشکی  </option>
                                    </select>               
                                    <div class="input-group-append">
                                        <span class="input-group-text"><i class="fa fa-stethoscope"></i></span>
                                    </div>
                                </div>
                            </div>

                        </form>
                    </div>
                    </ModalBody>
                    <ModalFooter>
                        <button className= "btn btn-secondary" style= {{width: "75px"}} onClick={this.props.onClose}>انصراف</button>
                        <button className= "btn btn-primary primary-button" style= {{width: "75px"}} onClick={this.sendData}>ذخیره</button>
                    </ModalFooter>
                </Modal>
            
        );
    }
}