import React from 'react';

import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

import './DoubleCheckModal.scss'
import 'bootstrap/dist/css/bootstrap.css'

export default class DoubleCheckModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    

    render() {
        return (
            
                <Modal size= "sm" isOpen={this.props.isOpen} className={this.props.className}>
                    <div className= "d-flex justify-content-center">
                        <ModalHeader>
                            {this.props.title}
                        </ModalHeader> 
                    </div>
                    <ModalBody>
                        <div style= {{direction: "rtl", "text-align": "right"}}>
                            {
                                typeof this.props.body == "undefined" ?
                                "آیا مطمئنید؟" :
                                this.props.body
                            }
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <div className= "d-flex flex-row-reverse justify-content-between align-items-end w-100">
                            <button className= "btn btn-primary btn-block ml-2 primary-button" onClick={() => {this.props.onYes(); this.props.onClose();}}>بله</button>
                            <button className= "btn btn-secondary btn-block default-button-shadow" onClick={this.props.onClose}>خیر</button>
                        </div>
                    </ModalFooter>
                </Modal>
            
        );
    }
}