import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './GameStage.scss';

import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {baseUrl, gameStageSendDataAPI} from '../../../../../APIData';
import SwitchOptionWrapper from '../SwitchOptionWrapper';
import Switch from '@material-ui/core/Switch';

export default class GameStage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            seconds: this.props.duration,
        }

        this.options = {
            car: true,
            game: true, 
            system: true,
        }
    }
    
    toggleAutomationState = (checked) => {
        this.setState({autoMode: checked});
    }

    sendOptions = () => {
        return axios.post(baseUrl + gameStageSendDataAPI, this.options, {
            headers: {token: localStorage.getItem("token")},
        })
        .then(res => {
            return true;
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }

            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            return false;
        });
    }

    finishStage = async () => {
        if (await this.sendOptions())
            this.props.notifyEnd();
    }

    render() {
        return (
            <div className= {`d-flex flex-column align-items-center w-100 stage-as-slide game 
                ${this.props.isCurrentStage ? " current" : this.props.isDead ? " dead" : ""}`}>
                <div className= "stage-control d-flex flex-column align-items-center">

                    <div class= "d-flex flex-column align-items-stretch">
                        <SwitchOptionWrapper title= "ماشین" className= "onoff-switch">
                                <BootstrapSwitchButton
                                    checked={true}
                                    onlabel='فعال'
                                    onstyle='success'
                                    offlabel='غیر فعال'
                                    offstyle='danger'
                                    style='w-100 mx-3'
                                    onChange={(checked) => {
                                        this.options.car = checked;
                                    }}
                                />
                        </SwitchOptionWrapper>

                        <SwitchOptionWrapper title= "بازی" className= "onoff-switch">
                                <BootstrapSwitchButton
                                    checked={true}
                                    onlabel='فعال'
                                    onstyle='success'
                                    offlabel='غیر فعال'
                                    offstyle='danger'
                                    style='w-100 mx-3'
                                    onChange={(checked) => {
                                        this.options.game = checked;
                                    }}
                                />
                        </SwitchOptionWrapper>

                        <SwitchOptionWrapper title= "سیستم خبره" className= "onoff-switch">
                                <BootstrapSwitchButton
                                    checked={true}
                                    onlabel='فعال'
                                    onstyle='success'
                                    offlabel='غیر فعال'
                                    offstyle='danger'
                                    style='w-100 mx-3'
                                    onChange={(checked) => {
                                        this.options.system = checked;
                                    }}
                                />
                        </SwitchOptionWrapper>

                    </div>
                </div>
            </div>
        );
    }
}