import React from 'react';
import { persianTime } from '../../../../utils';
import 'bootstrap/dist/css/bootstrap.css'
import './StageLayout.scss';
import Switch from '@material-ui/core/Switch';
import {CountDown} from '../../../CountDown';

export default class StageLayout extends React.Component {
    constructor(props) {
        super(props);
        this.state= {
            isTimerActive: true,
            timerResetToggle: true,
            navbarMinimized: true,
        };
    }

    render() {
        return(
            <div id= "stage-layout" className= {`d-flex py-md-4 px-md-5  py-0 px-0`}>
                <div class= "stage-wrap d-flex flex-column">
 
                    <div className= "d-flex flex-row-reverse h-100 w-100">
                        { this.props.stage.name != "PRE_START" ? 
                        <div className= {`stage-nav d-flex flex-column ${this.state.navbarMinimized ? " minimize" : ""}`}>
                            <div className= "nav-category">
                                <div className= "stage-nav-item-wrapper no-action"
                                   title= {this.state.navbarMinimized ? 
                                        `${this.props.stage.faName}\n زمان پیش‌فرض: ${persianTime(this.props.stage.duration)}`
                                        : ""
                                    }
                                >
                                    <i className= {`nav-icon  ${this.props.stage.iconClass}`}></i>
                                    <div className= "nav-item">
                                        {this.props.stage.faName}
                                    </div>
                                </div>
                                <div className= "stage-nav-item-wrapper no-action secondary-item">
                                    <i className= "nav-icon fas fa-clock"></i>
                                    <div className= "nav-item ">
                                        {persianTime(this.props.stage.duration)}
                                    </div>
                                </div>
                            </div>
                            <div className= "nav-category">
                             
                                <div className= "stage-nav-item-wrapper" onClick= {() => {this.setState({isTimerActive: false});}}
                                    title= {this.state.navbarMinimized ? 
                                        "توقف تایمر" : ""
                                    }
                                >
                                    <i class="nav-icon fas fa-stop"></i>
                                    <div className= "nav-item">
                                        توقف تایمر
                                    </div>

                                </div>

                                <div className= "stage-nav-item-wrapper" 
                                    onClick= {() => {this.setState({isTimerActive: true, timerResetToggle: !this.state.timerResetToggle});}}
                                    title= {this.state.navbarMinimized ? 
                                        "شروع مجدد تایمر" : ""
                                    }    
                                >
                                    <i class="nav-icon fas fa-undo"></i>
                                    <div className= "nav-item">
                                        شروع مجدد تایمر
                                    </div>

                                </div>

                            </div>
                            
                            <div className= "nav-category">
                                {
                                    this.props.stage.name == "PARROT_STAGE" ? 
                                        <div className= "stage-nav-item-wrapper" onClick= {this.props.onStageChange}
                                            title= {this.state.navbarMinimized ? 
                                                "اتمام تست (دستی)" : ""
                                            }        
                                        >
                                            <i class="nav-icon fas fa-check"></i>
                                            <div className= "nav-item">
                                                اتمام تست (دستی)
                                            </div>
                                        </div>
                                        :
                                        <div className= "stage-nav-item-wrapper" onClick= {this.props.onStageChange}
                                            title= {this.state.navbarMinimized ? 
                                                "مرحله بعد (دستی)" : ""
                                            }        
                                        >
                                            <i class="nav-icon fas fa-level-down-alt fa-rotate-90"></i>
                                            <div className= "nav-item">
                                                مرحله بعد (دستی)    
                                            </div>
                                        </div>
                                }
                                <div className= "stage-nav-item-wrapper" onClick= {this.props.onEndSession}
                                    title= {this.state.navbarMinimized ? 
                                        "لغو تست" : ""
                                    }                                        
                                >
                                    <i class="nav-icon fas fa-times"></i>
                                    <div className= "nav-item">
                                        لغو تست 
                                    </div>
                                </div>

                            </div>

                            <div className= "nav-category flex-grow-1 justify-content-end border-0">
                                <div className= "stage-nav-item-wrapper" onClick= {() => {this.setState({navbarMinimized: !this.state.navbarMinimized})}}>
                                    <i className={`nav-icon fas ${this.state.navbarMinimized ? " fa-angle-double-left" : " fa-angle-double-right"}`}></i>
                                </div>
                            </div>

                        </div>
                        : null }
                        <div className= "stage-body flex-grow-1 h-100 d-flex flex-column justify-content-start align-items-center w-100">
                            <div className= "d-flex flex-column align-items-center w-100 py-5 px-3 h-100">
                                { this.props.stage.name != "PRE_START" ? 
                                <div id= "timer">
                                    <CountDown key= {String(this.state.isTimerActive) + String(this.props.stage.name) + String(this.state.timerResetToggle)}  
                                        duration= {this.props.stage.duration}
                                        halt= {(this.state.isTimerActive == false)}
                                        notifyCountEnd= {this.props.onStageChange}
                                    />
                                </div> 
                                : null}
                                <div className= "stages-wrapper d-flex flex-row  mt-4">
                                    {this.props.children}
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        );
    }
}

