import React from 'react';
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import { withRouter } from "react-router-dom";
import {Layout} from '../Layout';
import {PatientInfo} from '../PatientInfo';
import {SessionControl} from './Session'
import {propertiesAPI, baseUrl, logoutAPI, verifyTokenAPI} from '../../APIData';
import './Control.scss';
import DoubleCheckModal from '../DoubleCheckModal/DoubleCheckModal';




class Control extends React.Component {
    constructor(props) {
        super(props);
        this.patient = {};
        
        this.state = {
            loading: true,
            showInfoBox: true,
            isLogoutDoubleCheckOpen: false,
            personName: "",
        }
        this.stage = null;
        this.siteProperties = null;
    }
    
    async verifyToken() {
        if (localStorage.getItem("token") == null) {
            this.props.history.push("/login");
            return false;
        }

        return axios.get(baseUrl + verifyTokenAPI, {
            headers: {
                token: localStorage.getItem("token"),
            }
        })
        .then(res => {
            if (res.data.login_status == false) {
                localStorage.removeItem("token");
                this.props.history.push('/login');    
            }
            return res.data.login_status
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }
            else if (String(error.response.status)[0] == 4) {
                localStorage.removeItem("token");
                this.props.history.push('/login');
            }
            else if (String(error.response.status)[0] == 5)
                ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            return false;
        });
    }


    fetchSiteProperties = async () => {
        return axios.get(baseUrl + propertiesAPI, {
            headers: {token: localStorage.getItem("token")},
        })
        .then(res => {
            this.siteProperties = res.data;
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }
            
            else if (String(error.response.status)[0] == 5)
            ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');

            else {
                ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            }
        });
    }

    async componentDidMount() {
        const isVerified = await this.verifyToken();
        if (!isVerified) {
            return;
        };
        await this.fetchSiteProperties();
        this.setState({loading: false});
    }

    toggleInfoBox = () => {
        this.setState({showInfoBox: !this.state.showInfoBox});
    }


    logout = () => {
        axios.post(baseUrl + logoutAPI, null, {
            headers: {token: localStorage.getItem("token")},
        })
        .then(res => {
            localStorage.removeItem("token");
            this.props.history.push('/login');
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }

    logoutRequest = () => {
        if (this.stage != 'PRE_START') {
            this.setState({isLogoutDoubleCheckOpen: true});
        }
        else this.logout();
    }


    render() {
        return (
            <Layout loading= {this.state.loading} collapseToggle= {this.toggleInfoBox} displayUser= {true}
                    onLogout= {this.logoutRequest} name= {this.state.personName}> 
                <div id= "Control" className= "row h-100">
                    <div id= "session-control-wrapper" className= {`px-0 ${this.state.showInfoBox ? " resized" : " w-100"}`} >
                        <SessionControl onStageChange= {(newStage) => {this.stage = newStage;} } siteProperties= {this.siteProperties} />
                    </div>
                        <div className="pl-0 pr-0 h-100">
                                <PatientInfo 
                                    showInfoBox= {this.state.showInfoBox} 
                                    person= {this.patient} 
                                    reloadData= {this.fetchUserData}
                                    onLogout= {this.logoutRequest}
                                    collapseToggle= {this.toggleInfoBox}
                                    onNameFetch = {(name) => {this.setState({personName: name});}}
                                />
                                <DoubleCheckModal 
                                    isOpen= {this.state.isLogoutDoubleCheckOpen}
                                    onClose= {() => {this.setState({isLogoutDoubleCheckOpen: false})}}
                                    onYes= {this.logout}
                                    title= "خروج؟"
                                    body= "شما در حال حاضر یک تست ناتمام دارید و با خروج امکان ادامه آن را نخواهید داشت. آیا مطمئن هستید؟"
                                />
                        </div>
                </div>
            </Layout>
        );
    }
}

export default withRouter(Control);
