export function isValidIranianNationalCode(input) {
    if (!/^\d{10}$/.test(input))
        return false;
 
    var check = parseInt(input[9]);
    var sum = 0;
    var i;
    for (i = 0; i < 9; ++i) {
        sum += parseInt(input[i]) * (10 - i);
    }
    sum %= 11;
 
    return (sum < 2 && check == sum) || (sum >= 2 && check + sum == 11);
}

export function mapObject(obj, mapFunction) {
    let mapped = {}
    for (const key in obj) {
        mapped[key] = mapFunction(obj[key]);
    }
    return mapped;
}

export function changeKeys(obj, keyMap) {
    let newDic = {};
    for (const key in obj) {
        newDic[keyMap[key]] = obj[key];
    }
    return newDic;
}

export function persianTime(seconds) {
    const mins = Math.floor(seconds/60);
    const secs = Math.floor(seconds%60);
    if (mins == 0)
        return `${secs} ثانیه`;
    else if (secs == 0)
        return `${mins} دقیقه`;
    else return `${mins} دقیقه و ${secs} ثانیه `;
}

